import React from 'react';
import Card from './Card'

const CardList = ({Robots})=>{
    const CardComponents = Robots.map((_,i)=>{
        return (<Card key={i} 
        id={Robots[i].id} 
        name={Robots[i].name} 
        email={Robots[i].email} 
        username={Robots[i].username}/>
    )})
    return(
        <div>
            {CardComponents}
        </div>

    );
}

export default CardList; 