import React,{Component} from 'react';
import CardList from '../Components/CardList'
// import {Robots} from './robots';
import SearchBox from '../Components/SearchBox.js';
import '../Containers/Application.css';
import Scroll from '../Components/Scroll';


class App extends Component{
    constructor(){
        super()
        this.state ={
            robots:[],
            seachField:''
        }
    }
    onSearchChange=(event)=>{
        this.setState({seachField:event.target.value})

        // console.log(event.target.value);
       
    }
    componentDidMount(){
        fetch('https://jsonplaceholder.typicode.com/users').then(res => res.json()).then(users=>
            this.setState({robots:users}));
    }
    render(){
        const filteredRobots = this.state.robots.filter(robot=>{
            return robot.username.toLocaleLowerCase().includes(this.state.seachField.toLocaleLowerCase())
            
        })
        return(
            <div className='tc'>
                <h1 className='f1'>Robo Friends</h1>
                <SearchBox searchChange={this.onSearchChange}/>
                <Scroll>
                <CardList Robots={filteredRobots}/>
                </Scroll>
            </div>
        )
    }
    
}
export default App;